@import '../../variables.scss';

.container {
  position: relative;
  width: 100%;
  min-height: 100vh;
  display: flex;
  background: hsla(0, 0%, 100%, 1);

  .image {
    position: relative;
    width: 50%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;

    &::before {
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      width: 100%;
      height: 100%;
      z-index: 1;
    }

    img {
      width: 75%;
      height: auto;
      object-fit: contain;
    }

    p {
      font-size: 2rem;
      font-weight: bold;
      text-transform: uppercase;
      color: rgba($color: $main-color, $alpha: 0.7);
    }
  }

  .content {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50%;
    height: 100%;
    background-color: rgba($color: $main-color, $alpha: 1);
  }
}

@media (max-width: 768px) {
  .container {
    .image {
      position: absolute;
      width: 100%;
      height: 100%;
    }

    .content {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 100%;
      height: 100%;
      z-index: 1;
      background: linear-gradient(0deg, hsla(0, 0%, 100%, 1) 0%, $main-color 60%);
    }
  }
}