@import "../../variables.scss";

.container {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.background_div {
  width: 100vw;
  height: 100vh;
  position: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

  opacity: 0.1;
}

.content_area {
  position: relative;
  flex: 1;
  z-index: 1;
}

.hamburger_menu {
  height: 60px;
  background-color: $main-color;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: sticky;
  top: 0;
  z-index: 10;
}

.hamburger_menu p {
  color: $white-color;
  font-size: 0.8rem;
  padding-inline-start: 10px;
}

.toTop {
  position: fixed;
  left: 10px;
  bottom: 10px;
  font-size: 2rem;
  color: $yellow-color;
  opacity: 0.6;
  cursor: pointer;
  transition: all 0.3s;
  z-index: 100;
}

.toTop:hover {
  opacity: 1;
  transform: scale(1.1);
}

@media only screen and (min-width: $small) {
  .hamburger_menu {
    display: none;
  }
}