// end text align

// style for icon div
.icon {
  width: 32px;
  height: 32px;

  padding: 4px;

  display: flex;
  justify-content: center;
  align-items: center;

  position: relative;

  cursor: pointer;
}

.icon:hover {
  border-radius: 50%;
  background: rgba($color: #000000, $alpha: 0.1) !important;
  box-shadow: inset 2px 2px 2px 1px rgba(0, 0, 0, 0.1);
  border-radius: 3px;
}

.tooltip {
  display: none;

  min-width: 100px;
  font-size: 0.7rem;
  padding: 2px 8px;

  background-color: rgba($color: #000000, $alpha: 0.8);
  color: $white-color;

  border-radius: 6px;

  position: absolute;
  top: 35px;

  z-index: 50;
}



.icon:hover .tooltip {
  display: flex;
  justify-content: center;
  align-items: center;
}

// end of style for icon div\
.margin_h_auto {
  margin: 0 auto;
}

.minus_top_margin {
  margin-top: -4px;
}

.margin_top_10 {
  margin-top: 10px;
}

.position_top_5_left_5 {
  position: absolute;
  top: 5px;
  left: 5px;
}

.position_top_5_left_40 {
  position: absolute;
  top: 5px;
  left: 40px;
}

.empty {
  width: 400px;
  height: 400px;

  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;

  border-radius: 50%;

  margin-top: 30px;
}

// button style
.button {
  outline: none;
  border: none;

  border-radius: 6px;

  cursor: pointer;
}

.button:hover {
  transform: scale(1.1);
  text-shadow: 0 0 8px rgb(255, 255, 255);
  box-shadow: 0 0 8px rgb(255, 255, 255);
}

// end button style

