.block {
  display: block;
}

.relative {
  position: relative;
}

.actions {
  display: flex;
  justify-content: center;
  margin-block-end: 10px;
}

// style the div that contains the favorite row
.favorites_content {
  min-width: 200px;
  max-height: 300px;

  box-shadow: 0 0 3px rgba($color: $secondary-color, $alpha: 0.5);
  border-radius: 6px;

  position: absolute;
  top: 25px;
  transform: translateX(125px);

  overflow: auto;
  z-index: 10;
}

.favorites_content_wider {
  min-width: 350px;
}

div.count {
  text-align: center;
  color: $main-color;
  font-size: 1.1rem;
  font-weight: bold;
}

span.label {
  font-size: 0.9rem;
  color: $grey-color;
}

span.count {
  font-size: 1.2rem;
  font-weight: bold;
  color: $main-color;
  padding-inline: 8px;
}

// style the div that display an icon the indicates that there is no result
.no_content_div {
  width: 50%;
  height: 200px;
  margin: 50px auto;
  // background: rgba($color: $secondary-color, $alpha: 0.5);
  border-radius: 6px;

  // box-shadow: 3px 3px 3px rgba($color: #000000, $alpha: 0.3);

  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.no_content_icon {
  font-size: 15rem;
  margin: 10px 0;
  color: $white-color;

  animation-name: rotate-icon;
  animation-duration: 3s;
  animation-iteration-count: infinite;
  animation-direction: alternate-reverse;
}

@keyframes rotate-icon {
  0% {
    transform: rotateZ(0deg);
  }

  100% {
    transform: rotateZ(180deg);
  }
}

// end of style the div that display an icon the indicates that there is no result

.flex_container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
}

.flex_center_container {
  display: flex;
  align-items: center;
  justify-content: center;
}

.flex_column {
  flex-direction: column;
}

// loading icon
.loading {
  animation-name: loading-move;
  animation-duration: 1s;
  animation-iteration-count: infinite;
}

@keyframes loading-move {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

// no internet connection
.no_internet_container {
  width: 250px;
  min-width: 250px;

  position: fixed;
  bottom: 50px;
  left: 20px;

  color: #fff;
  background: rgba(0, 0, 0, 0.9);
  box-shadow: 2px 2px 3px 0 rgba(0, 0, 0, 0.5);

  padding: 6px;

  border-radius: 6px;

  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;

  z-index: 50;
}

.header_title {
  margin-top: 16px !important;
  font-size: 0.9rem;
}

@media only screen and (min-width: $medium) {
  .header_title {
    font-size: 1.5rem;
  }


}